// pages/users.js
import { useMemo, useState } from 'react'
import Layout from '../components/Layout'
import theme from '../theme'
import toast from 'react-hot-toast'

// üîπ ÿ™ÿπÿ±ŸäŸÅ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ ÿßŸÑŸÖÿ™ÿßÿ≠ÿ©
const ALL_PERMISSIONS = [
  { key: 'view_reports', label: 'ÿπÿ±ÿ∂ ÿßŸÑÿ™ŸÇÿßÿ±Ÿäÿ±' },
  { key: 'add_sale', label: 'ÿ•ÿ∂ÿßŸÅÿ© ÿπŸÖŸÑŸäÿ© ÿ®Ÿäÿπ' },
  { key: 'manage_medicines', label: 'ÿ•ÿØÿßÿ±ÿ© ÿßŸÑÿ£ÿØŸàŸäÿ©' },
  { key: 'manage_users', label: 'ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ' },
  { key: 'view_inventory', label: 'ÿπÿ±ÿ∂ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ' },
]

// üîπ ÿ£ÿ≥ŸÖÿßÿ° ÿßŸÑÿ£ÿØŸàÿßÿ± ŸÑŸÑÿπÿ±ÿ∂
const ROLE_LABELS = {
  admin: 'ŸÖÿØŸäÿ± ÿßŸÑŸÜÿ∏ÿßŸÖ',
  pharmacist: 'ÿµŸäÿØŸÑŸä',
  cashier: 'ŸÉÿßÿ¥Ÿäÿ±',
}

// üîπ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ ÿßŸÑÿßŸÅÿ™ÿ±ÿßÿ∂Ÿäÿ© ŸÑŸÉŸÑ ÿØŸàÿ±
const ROLE_DEFAULT_PERMISSIONS = {
  admin: ALL_PERMISSIONS.map((p) => p.key),
  pharmacist: ['manage_medicines', 'view_inventory', 'add_sale', 'view_reports'],
  cashier: ['add_sale'],
}

// üîπ ÿ®ŸäÿßŸÜÿßÿ™ ÿ™ÿ¨ÿ±Ÿäÿ®Ÿäÿ© (Mock)
const INITIAL_USERS = [
  {
    id: 1,
    name: 'ŸÖŸáÿß ÿπŸÑŸä',
    username: 'admin',
    email: 'admin@pharmacy.com',
    role: 'admin',
    active: true,
    permissions: ROLE_DEFAULT_PERMISSIONS.admin,
  },
  {
    id: 2,
    name: 'ÿ£ÿ≠ŸÖÿØ ÿßŸÑÿµŸäÿØŸÑŸä',
    username: 'pharma',
    email: 'pharma@pharmacy.com',
    role: 'pharmacist',
    active: true,
    permissions: ROLE_DEFAULT_PERMISSIONS.pharmacist,
  },
  {
    id: 3,
    name: 'ŸÖÿ≠ŸÖÿØ ÿßŸÑŸÉÿßÿ¥Ÿäÿ±',
    username: 'cashier',
    email: 'cashier@pharmacy.com',
    role: 'cashier',
    active: true,
    permissions: ROLE_DEFAULT_PERMISSIONS.cashier,
  },
]

export default function UsersPage() {
  // ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿßŸÑÿ≠ÿßŸÑŸä (ŸÖÿØŸäÿ±)
  const [user] = useState({ name: 'ÿßŸÑŸÖÿØŸäÿ± ÿ£ÿ≠ŸÖÿØ', role: 'admin' })

  const [users, setUsers] = useState(INITIAL_USERS)
  const [search, setSearch] = useState('')

  const [showAddModal, setShowAddModal] = useState(false)
  const [showPermModal, setShowPermModal] = useState(false)

  const [newUser, setNewUser] = useState({
    name: '',
    username: '',
    email: '',
    password: '',
    role: 'cashier',
    active: true,
  })

  const [selectedUser, setSelectedUser] = useState(null)
  const [permDraft, setPermDraft] = useState([])

  // =============== ŸÅŸÑÿ™ÿ±ÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ ===============
  const filteredUsers = useMemo(() => {
    const q = search.trim().toLowerCase()
    if (!q) return users
    return users.filter(
      (u) =>
        u.name?.toLowerCase().includes(q) ||
        u.username?.toLowerCase().includes(q) ||
        u.email?.toLowerCase().includes(q)
    )
  }, [users, search])

  // =============== ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ ===============
  const openAddModal = () => {
    setNewUser({
      name: '',
      username: '',
      email: '',
      password: '',
      role: 'cashier',
      active: true,
    })
    setShowAddModal(true)
  }

  const handleAddUser = () => {
    if (!newUser.name || !newUser.username || !newUser.email || !newUser.password) {
      toast.error('‚ö†Ô∏è Ÿäÿ±ÿ¨Ÿâ ÿ•ÿØÿÆÿßŸÑ ÿ¨ŸÖŸäÿπ ÿßŸÑÿ≠ŸÇŸàŸÑ ÿßŸÑÿ£ÿ≥ÿßÿ≥Ÿäÿ©')
      return
    }

    const nextId = users.length ? Math.max(...users.map((u) => u.id)) + 1 : 1

    const userToAdd = {
      id: nextId,
      name: newUser.name,
      username: newUser.username,
      email: newUser.email,
      role: newUser.role,
      active: true,
      // ŸÅŸä ÿßŸÑÿ®ÿßŸÉ ÿ•ŸÜÿØ ŸÑÿßÿ≠ŸÇŸãÿß: ŸäŸÖŸÉŸÜ ÿ≠ÿ≥ÿßÿ®Ÿáÿß ÿ£Ÿà ÿ≠ŸÅÿ∏Ÿáÿß ŸÅŸä ÿ¨ÿØŸàŸÑ ŸÖŸÜŸÅÿµŸÑ
      permissions: ROLE_DEFAULT_PERMISSIONS[newUser.role] || [],
    }

    setUsers((prev) => [...prev, userToAdd])
    setShowAddModal(false)
    toast.success('‚úÖ ÿ™ŸÖ ÿ•ÿ∂ÿßŸÅÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿ®ŸÜÿ¨ÿßÿ≠ (ÿ®ŸäÿßŸÜÿßÿ™ ÿ™ÿ¨ÿ±Ÿäÿ®Ÿäÿ©)')
  }

  // =============== ÿ™ŸÅÿπŸäŸÑ / ÿ™ÿπÿ∑ŸäŸÑ ===============
  const toggleActive = (id) => {
    setUsers((prev) =>
      prev.map((u) => (u.id === id ? { ...u, active: !u.active } : u))
    )
    toast.success('üîÅ ÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿ´ ÿ≠ÿßŸÑÿ© ÿßŸÑÿ≠ÿ≥ÿßÿ®')
  }

  // =============== ÿ≠ÿ∞ŸÅ ŸÖÿ≥ÿ™ÿÆÿØŸÖ ===============
  const deleteUser = (id) => {
    if (!confirm('ŸáŸÑ ÿ™ÿ±ŸäÿØ ÿ≠ÿ∞ŸÅ Ÿáÿ∞ÿß ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ŸÜŸáÿßÿ¶ŸäŸãÿßÿü')) return
    setUsers((prev) => prev.filter((u) => u.id !== id))
    toast.success('üóëÔ∏è ÿ™ŸÖ ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ')
  }

  // =============== ÿ•ÿØÿßÿ±ÿ© ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ ===============
  const openPermModal = (u) => {
    setSelectedUser(u)
    setPermDraft(u.permissions || [])
    setShowPermModal(true)
  }

  const togglePermission = (key) => {
    setPermDraft((prev) =>
      prev.includes(key) ? prev.filter((p) => p !== key) : [...prev, key]
    )
  }

  const savePermissions = () => {
    if (!selectedUser) return
    setUsers((prev) =>
      prev.map((u) =>
        u.id === selectedUser.id ? { ...u, permissions: permDraft } : u
      )
    )
    setShowPermModal(false)
    toast.success('üîê ÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿ´ ÿµŸÑÿßÿ≠Ÿäÿßÿ™ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ')
  }

  // =============== UI ===============
  return (
    <Layout user={user} title="üë• ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ ŸàÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™">
      <div dir="rtl" className="space-y-6">
        {/* ÿ¥ÿ±Ÿäÿ∑ ÿπŸÑŸàŸä: ÿ®ÿ≠ÿ´ + ÿ•ÿ∂ÿßŸÅÿ© */}
        <div className="flex flex-col gap-3 p-4 bg-white border rounded-lg shadow-sm md:flex-row md:items-center md:justify-between">
          <input
            type="text"
            placeholder="üîç ÿ®ÿ≠ÿ´ ÿ®ÿßŸÑÿßÿ≥ŸÖ / ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ / ÿßŸÑÿ®ÿ±ŸäÿØ"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="w-full px-3 py-2 text-sm border rounded-md md:w-1/2"
          />
          <button
            onClick={openAddModal}
            className="px-4 py-2 text-sm text-white rounded-md shadow"
            style={{ background: theme.colors.success }}
          >
            ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ
          </button>
        </div>

        {/* ÿ¨ÿØŸàŸÑ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ */}
        <div className="overflow-x-auto bg-white border rounded-lg shadow-sm">
          <table className="w-full text-sm text-right min-w-[850px]">
            <thead className="text-xs text-gray-600 bg-gray-50">
              <tr>
                <th className="px-3 py-2">#</th>
                <th className="px-3 py-2">ÿßŸÑÿßÿ≥ŸÖ</th>
                <th className="px-3 py-2">ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ</th>
                <th className="px-3 py-2">ÿßŸÑÿ®ÿ±ŸäÿØ</th>
                <th className="px-3 py-2">ÿßŸÑÿØŸàÿ±</th>
                <th className="px-3 py-2">ÿßŸÑÿ≠ÿßŸÑÿ©</th>
                <th className="px-3 py-2">ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ ÿßŸÑÿ£ÿ≥ÿßÿ≥Ÿäÿ©</th>
                <th className="px-3 py-2">ÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
              </tr>
            </thead>
            <tbody>
              {filteredUsers.length ? (
                filteredUsers.map((u, i) => (
                  <tr key={u.id} className="border-t hover:bg-gray-50">
                    <td className="px-3 py-2">{i + 1}</td>
                    <td className="px-3 py-2 font-medium text-gray-800">{u.name}</td>
                    <td className="px-3 py-2 text-gray-700">{u.username}</td>
                    <td className="px-3 py-2 text-gray-600">{u.email}</td>
                    <td className="px-3 py-2">
                      <span
                        className={`inline-flex items-center rounded-full px-3 py-1 text-xs font-semibold ${
                          u.role === 'admin'
                            ? 'bg-purple-100 text-purple-700'
                            : u.role === 'pharmacist'
                            ? 'bg-blue-100 text-blue-700'
                            : 'bg-emerald-100 text-emerald-700'
                        }`}
                      >
                        {ROLE_LABELS[u.role] || u.role}
                      </span>
                    </td>
                    <td className="px-3 py-2">
                      <span
                        className={`inline-flex items-center px-2.5 py-1 text-xs font-semibold rounded-full ${
                          u.active
                            ? 'bg-emerald-100 text-emerald-700'
                            : 'bg-red-100 text-red-700'
                        }`}
                      >
                        {u.active ? 'ŸÖŸÅÿπŸÑ' : 'ŸÖŸàŸÇŸàŸÅ'}
                      </span>
                    </td>
                    <td className="px-3 py-2">
                      <div className="flex flex-wrap gap-1 text-[11px]">
                        {(u.permissions || []).slice(0, 3).map((p) => {
                          const perm = ALL_PERMISSIONS.find((x) => x.key === p)
                          return (
                            <span
                              key={p}
                              className="px-2 py-0.5 rounded-full bg-sky-50 text-sky-700"
                            >
                              {perm?.label || p}
                            </span>
                          )
                        })}
                        {u.permissions && u.permissions.length > 3 && (
                          <span className="px-2 py-0.5 rounded-full bg-gray-100 text-gray-500">
                            +{u.permissions.length - 3} ÿ£ÿÆÿ±Ÿâ
                          </span>
                        )}
                      </div>
                    </td>
                    <td className="px-3 py-2">
                      <div className="flex flex-wrap justify-center gap-2">
                        <button
                          onClick={() => openPermModal(u)}
                          className="px-3 py-1 text-xs text-indigo-700 rounded-md bg-indigo-50 hover:bg-indigo-100"
                        >
                          üîê ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™
                        </button>
                        <button
                          onClick={() => toggleActive(u.id)}
                          className={`px-3 py-1 text-xs rounded-md ${
                            u.active
                              ? 'bg-red-50 text-red-600 hover:bg-red-100'
                              : 'bg-emerald-50 text-emerald-700 hover:bg-emerald-100'
                          }`}
                        >
                          {u.active ? 'üîí ÿ™ÿπÿ∑ŸäŸÑ' : 'üîì ÿ™ŸÅÿπŸäŸÑ'}
                        </button>
                        <button
                          onClick={() => deleteUser(u.id)}
                          className="px-3 py-1 text-xs text-red-600 bg-white border border-red-200 rounded-md hover:bg-red-50"
                        >
                          üóëÔ∏è ÿ≠ÿ∞ŸÅ
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td
                    colSpan={8}
                    className="px-3 py-6 text-sm text-center text-gray-500"
                  >
                    ŸÑÿß ÿ™Ÿàÿ¨ÿØ ŸÜÿ™ÿßÿ¶ÿ¨
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* ŸÖŸàÿØÿßŸÑ ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ */}
      {showAddModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40">
          <div className="w-full max-w-md p-6 bg-white rounded-lg shadow-lg" dir="rtl">
            <h2 className="mb-4 text-lg font-bold text-center text-gray-800">
              ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿ¨ÿØŸäÿØ
            </h2>

            <div className="space-y-3 text-sm">
              <Field label="ÿßŸÑÿßÿ≥ŸÖ ÿßŸÑŸÉÿßŸÖŸÑ">
                <input
                  type="text"
                  value={newUser.name}
                  onChange={(e) => setNewUser({ ...newUser, name: e.target.value })}
                  className="w-full px-3 py-2 border rounded-md"
                />
              </Field>

              <Field label="ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ">
                <input
                  type="text"
                  value={newUser.username}
                  onChange={(e) =>
                    setNewUser({ ...newUser, username: e.target.value })
                  }
                  className="w-full px-3 py-2 border rounded-md"
                />
              </Field>

              <Field label="ÿßŸÑÿ®ÿ±ŸäÿØ ÿßŸÑÿ•ŸÑŸÉÿ™ÿ±ŸàŸÜŸä">
                <input
                  type="email"
                  value={newUser.email}
                  onChange={(e) => setNewUser({ ...newUser, email: e.target.value })}
                  className="w-full px-3 py-2 border rounded-md"
                />
              </Field>

              <Field label="ŸÉŸÑŸÖÿ© ÿßŸÑŸÖÿ±Ÿàÿ±">
                <input
                  type="password"
                  value={newUser.password}
                  onChange={(e) =>
                    setNewUser({ ...newUser, password: e.target.value })
                  }
                  className="w-full px-3 py-2 border rounded-md"
                />
              </Field>

              <Field label="ÿßŸÑÿØŸàÿ±">
                <select
                  value={newUser.role}
                  onChange={(e) => setNewUser({ ...newUser, role: e.target.value })}
                  className="w-full px-3 py-2 border rounded-md"
                >
                  <option value="admin">ŸÖÿØŸäÿ±</option>
                  <option value="pharmacist">ÿµŸäÿØŸÑŸä</option>
                  <option value="cashier">ŸÉÿßÿ¥Ÿäÿ±</option>
                </select>
              </Field>
            </div>

            <div className="flex justify-end gap-2 mt-5">
              <button
                onClick={() => setShowAddModal(false)}
                className="px-4 py-2 text-sm bg-gray-100 rounded-md hover:bg-gray-200"
              >
                ÿ•ŸÑÿ∫ÿßÿ°
              </button>
              <button
                onClick={handleAddUser}
                className="px-4 py-2 text-sm text-white rounded-md shadow"
                style={{ background: theme.colors.primary }}
              >
                üíæ ÿ≠ŸÅÿ∏
              </button>
            </div>
          </div>
        </div>
      )}

      {/* ŸÖŸàÿØÿßŸÑ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ */}
      {showPermModal && selectedUser && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40">
          <div className="w-full max-w-lg p-6 bg-white rounded-lg shadow-lg" dir="rtl">
            <h2 className="mb-2 text-lg font-bold text-gray-800">
              üîê ÿµŸÑÿßÿ≠Ÿäÿßÿ™ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ: {selectedUser.name}
            </h2>
            <p className="mb-4 text-xs text-gray-500">
              ÿßŸÑÿØŸàÿ± ÿßŸÑÿ≠ÿßŸÑŸä: <strong>{ROLE_LABELS[selectedUser.role]}</strong> ‚Äî ŸäŸÖŸÉŸÜŸÉ
              ÿ™ÿÆÿµŸäÿµ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™ ŸäÿØŸàŸäŸãÿß ÿ≠ÿ≥ÿ® ÿßŸÑÿ≠ÿßÿ¨ÿ©.
            </p>

            <div className="grid grid-cols-1 gap-2 text-sm sm:grid-cols-2">
              {ALL_PERMISSIONS.map((perm) => (
                <label
                  key={perm.key}
                  className="flex items-center gap-2 px-2 py-1 border rounded-md cursor-pointer hover:bg-gray-50"
                >
                  <input
                    type="checkbox"
                    checked={permDraft.includes(perm.key)}
                    onChange={() => togglePermission(perm.key)}
                  />
                  <span>{perm.label}</span>
                </label>
              ))}
            </div>

            <div className="flex justify-end gap-2 mt-5">
              <button
                onClick={() => setShowPermModal(false)}
                className="px-4 py-2 text-sm bg-gray-100 rounded-md hover:bg-gray-200"
              >
                ÿ•ŸÑÿ∫ÿßÿ°
              </button>
              <button
                onClick={savePermissions}
                className="px-4 py-2 text-sm text-white rounded-md shadow"
                style={{ background: theme.colors.primary }}
              >
                üíæ ÿ≠ŸÅÿ∏ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™
              </button>
            </div>
          </div>
        </div>
      )}
    </Layout>
  )
}

// ŸÖŸÉŸàŸëŸÜ ÿ®ÿ≥Ÿäÿ∑ ŸÑÿ™Ÿàÿ≠ŸäÿØ ÿ¥ŸÉŸÑ ÿßŸÑÿ≠ŸÇŸàŸÑ ÿØÿßÿÆŸÑ ÿßŸÑŸÖŸàÿØÿßŸÑ
function Field({ label, children }) {
  return (
    <div>
      <label className="block mb-1 text-xs font-semibold text-gray-600">
        {label}
      </label>
      {children}
    </div>
  )
}









